import React from "react";

const processData = [
  {
    title: "Kontakt i wycena",
    icon: "fa-phone",
    text:
      "Zadzwo lub wypenij formularz - oddzwonimy, ustalimy termin i przygotujemy darmow wycene.",
  },
  {
    title: "Spotkanie i oferta",
    icon: "fa-house-circle-check",
    text:
      "Przyjdziemy na miejsce, sprawdzimy stan cian i ustalimy zakres prac. Otrzymujesz konkretn ofert bez ukrytych kosztw.",
  },
  {
    title: "Dobr kolorw",
    icon: "fa-palette",
    text:
      "Masz wtpliwoci? Pomoemy dobra kolory idealnie dopasowane do wntrza",
  },
  {
    title: "Przygotowanie i malowanie",
    icon: "fa-paint-roller",
    text:
      "Zabezpieczamy pomieszczenia, przesuwamy meble, naprawiamy drobne ustarki - i dziaamy",
  },
  {
    title: "Efekt kocowy",
    icon: "fa-border-all",
    text:
      "Malujemy precyzyjnie i czysto, wewntrz i na zewntrz. Gwarantujemy trway efekt.",
  },
  {
    title: "Sprztanie i odbir",
    icon: "fa-brush",
    text:
      "Po pracy zostawiamy porzdek i wsplnie odbieramy zlecenie. Bez poprawek - jest zrobione raz, a dobrze",
  },
];

const Proces = () => {
  return (
    <div className="process" style={{ overflowX: "auto" }}>
      <div className="container ui segment process-container">
        <div className="estimate-title-box">PRZEBIEG WSPPRACY</div>
        <hr className="line" />

        {processData.map((item, index) => (
          <div className="process-box" key={index}>
            <p className="process-titles">
              <i className={`fa-solid ${item.icon}`}></i> {item.title}
            </p>
            <p className="process-p">{item.text}</p>
            {index !== processData.length - 1 && (
              <div className="arrow-down">
                <i className="fa-solid fa-arrow-down"></i>
              </div>
            )}
          </div>
        ))}
      </div>
    </div>
  );
};

export default Proces;
